import React, { Component } from "react";
import {
  Space,
  Button,
  Table,
  Timeline,
  message,
  Tag,
  Pagination,
  Modal,
  Input,
  Form,
  Cascader,
  Tooltip,
} from "antd";
import { CarOutlined, GlobalOutlined } from "@ant-design/icons";
import moment from "moment";
import axios from "axios";
import CityData from "./CityData";
const { Search } = Input;
export default class OrderList extends Component {
  editShippingAddressFormRef = React.createRef();
  editShippingAddressFormRules = {
    areaAddress: [{ required: true, message: "" }],
    detailAddress: [{ required: true, message: "" }],
  };
  columns = [
    {
      title: "#",
      key: "index",
      render: (text, record, index) => index + 1,
    },
    {
      width: "20%",
      title: "",
      dataIndex: "order_number",
      key: "order_number",
    },
    {
      width: "10%",
      title: "",
      dataIndex: "order_price",
      key: "order_price",
    },
    {
      width: "10%",
      title: "",
      key: "pay_status",
      render: (text, record) =>
        record.status === 1 ? <Tag color="green"></Tag> : <Tag color="red"></Tag>,
    },
    {
      width: "10%",
      title: "",
      key: "is_send",
      render: (text, record) => (
        <Tag color={record.is_send === "" ? "green" : "red"}>{record.is_send}</Tag>
      ),
    },
    {
      width: "20%",
      title: "",
      key: "consignee_addr",
      dataIndex: "consignee_addr",
    },
    {
      width: "15%",
      title: "",
      key: "mg_state",
      render: (text, record) => moment(record.create_time).format("YYYY-MM-DD HH:mm:ss"),
    },
    {
      width: "10%",
      title: "",
      key: "operation",
      render: (text, record) => (
        <Space>
          <Tooltip title="" color="blue">
            <Button
              icon={<GlobalOutlined />}
              className="primaryStyle"
              onClick={this.showShippingAddressModal.bind(this, record)}
            />
          </Tooltip>
          <Tooltip title="" color="orange">
            <Button
              icon={<CarOutlined />}
              className="warningStyle"
              onClick={this.showExpressProgressModal}
            />
          </Tooltip>
        </Space>
      ),
    },
  ];
  state = {
    orderList: [],
    orderTotal: 0,
    // 
    queryInfo: {
      query: "",
      pagenum: 1,
      pagesize: 10,
    },
    shippingAddressModalVisible: false,
    expressProgressModalVisible: false,
    // ID
    order_id: "",
    order_price: "",
    editShippingAddressForm: {
      areaAddress: [],
      detailAddress: "",
    },
    expressProgressInfo: [
      {
        time: "2018-05-10 09:39:00",
        ftime: "2018-05-10 09:39:00",
        context: ",,",
        location: "",
      },
      {
        time: "2018-05-10 08:23:00",
        ftime: "2018-05-10 08:23:00",
        context: "[]  95338",
        location: "",
      },
      {
        time: "2018-05-10 07:32:00",
        ftime: "2018-05-10 07:32:00",
        context: " []",
        location: "",
      },
      {
        time: "2018-05-10 02:03:00",
        ftime: "2018-05-10 02:03:00",
        context: "[], []",
        location: "",
      },
      {
        time: "2018-05-09 23:05:00",
        ftime: "2018-05-09 23:05:00",
        context: " []",
        location: "",
      },
      {
        time: "2018-05-09 21:21:00",
        ftime: "2018-05-09 21:21:00",
        context: "[], []",
        location: "",
      },
      {
        time: "2018-05-09 13:07:00",
        ftime: "2018-05-09 13:07:00",
        context: " ",
        location: "",
      },
      {
        time: "2018-05-09 12:25:03",
        ftime: "2018-05-09 12:25:03",
        context: "",
        location: "",
      },
      {
        time: "2018-05-09 12:22:24",
        ftime: "2018-05-09 12:22:24",
        context: "HLA",
        location: "",
      },
      {
        time: "2018-05-08 21:36:04",
        ftime: "2018-05-08 21:36:04",
        context: "",
        location: "",
      },
    ],
    loading: false,
  };
  showShippingAddressModal = async ({ order_id, order_price }) => {
    try {
      const { data: res } = await axios.get(`/orders/${order_id}`);
      if (res.meta.status !== 200) {
        return message.error("");
      }
      const shippingAddress = res.data.consignee_addr.split(" ");
      const areaAddress = shippingAddress[0].match(/.+?(|||||)/g);
      const detailAddress = shippingAddress[1];
      this.setState({
        shippingAddressModalVisible: true,
        editShippingAddressForm: { areaAddress, detailAddress },
        order_id,
        order_price,
      });
    } catch (error) {}
  };
  showExpressProgressModal = async () => {
    try {
      // const {data:res} = await axios.get('kuaidi/1106975712662');
      // if(res.meta.status !==200){
      //   return message.error("");
      // }
      // this.setState({expressProgressModalVisible:true,expressProgressInfo:res.data})
      this.setState({ expressProgressModalVisible: true });
    } catch (error) {}
  };
  // pagesize
  onPageChange = (page, pagesize) => {
    this.state.queryInfo.pagenum = page;
    this.state.queryInfo.pagesize = pagesize;
    this.getOrderList();
  };
  resetSearch = (event) => {
    if (event.target.value === "") {
      this.state.queryInfo.query = "";
      this.getOrderList();
    }
  };
  searchOrder = (order_number) => {
    this.state.queryInfo.query = order_number;
    this.getOrderList();
  };
  getOrderList = async () => {
    this.setState({ loading: true });
    try {
      const { data: res } = await axios.get("orders", { params: this.state.queryInfo });

      if (res.meta.status !== 200) {
        return message.error("");
      }
      this.setState({ orderTotal: res.data.total, orderList: res.data.goods, loading: false });
    } catch (error) {}
  };
  editShippingAddress = async () => {
    try {
      this.editShippingAddressFormRef.current
        .validateFields()
        .then(async ({ areaAddress, detailAddress }) => {
          const consignee_addr = areaAddress.join("") + " " + detailAddress;
          const { data: res } = await axios.put(`orders/${this.state.order_id}`, {
            consignee_addr,
            order_price: this.state.order_price,
          });
          if (res.meta.status !== 201) {
            return message.error("");
          }
          message.success("");
          this.setState({ shippingAddressModalVisible: false });
          this.getOrderList();
        });
    } catch (error) {}
  };
  componentDidMount() {
    this.getOrderList();
  }
  render() {
    return (
      <>
        <Space direction="vertical" size="middle">
          <Search
            style={{ width: "300px" }}
            size="large"
            allowClear
            placeholder=""
            onChange={this.resetSearch}
            onSearch={this.searchOrder}
          />
          <Table
            loading={this.state.loading}
            pagination={{ position: ["none"] }}
            columns={this.columns}
            dataSource={this.state.orderList}
            bordered
            rowKey={(record) => record.order_id}
          />
          <Pagination
            total={this.state.orderTotal}
            current={this.state.queryInfo.pagenum}
            size="large"
            pageSizeOptions={[1, 2, 5, 10]}
            defaultPageSize={this.state.queryInfo.pagesize}
            showSizeChanger
            onChange={this.onPageChange}
            showQuickJumper
            showTotal={(total) => ` ${total} `}
          />
        </Space>
        <Modal
          title=""
          visible={this.state.shippingAddressModalVisible}
          onCancel={() => {
            this.setState({ shippingAddressModalVisible: false });
          }}
          onOk={this.editShippingAddress}
          destroyOnClose
        >
          <Form
            ref={this.editShippingAddressFormRef}
            initialValues={this.state.editShippingAddressForm}
          >
            <Form.Item
              label="/"
              labelCol={{ span: 5 }}
              rules={this.editShippingAddressFormRules.areaAddress}
              name="areaAddress"
            >
              <Cascader options={CityData} expandTrigger="hover" placeholder="" />
            </Form.Item>
            <Form.Item
              label=""
              labelCol={{ span: 5 }}
              rules={this.editShippingAddressFormRules.detailAddress}
              name="detailAddress"
            >
              <Input />
            </Form.Item>
          </Form>
        </Modal>
        <Modal
          visible={this.state.expressProgressModalVisible}
          onCancel={() => {
            this.setState({ expressProgressModalVisible: false });
          }}
          footer={null}
        >
          <Timeline>
            {this.state.expressProgressInfo.map((item, index) => {
              return (
                <Timeline.Item key={index}>
                  {item.context} {item.time}
                </Timeline.Item>
              );
            })}
          </Timeline>
        </Modal>
      </>
    );
  }
}
